import React from "react";
import { Paper, Divider } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import moment from "moment";
import { jsxDecorator } from "storybook-addon-jsx";
import { muiTheme } from "storybook-addon-material-ui";
import "react-dates/initialize";
import "react-dates/lib/css/_datepicker.css";
import Theme from "../../../Theme";
import themes from "../../../Theme/themes";
import { styles } from "../../styles";
import Calendar2days from "../Calendar2days";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

export const DefaultCalendar2days = ({
    mode, brand, isMobile
}) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="calendar">
                <Typography variant="h3">Calendar2days</Typography>
                <br />
                <Divider />
                <br />

                <Calendar2days
                    onDatesChange={() => {}}
                    startDate={moment()}
                    endDate={moment().add(4, "days")}
                    isMobile={isMobile}
                />
            </Paper>
        </MuiThemeProvider>
    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        isMobile: false
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        isMobile: { type: "boolean" }
    }
}

storiesOf("Components/Calendars/Calendar2days", module)
    .add("Default ", (args) => <DefaultCalendar2days  {...args}/>, argsControls);
